@import url("https://fonts.googleapis.com/css2?family=Hachi+Maru+Pop&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,400;0,500;1,100;1,200;1,300;1,400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(9, 8, 8);
}

header {
  background-image: linear-gradient(
    90deg,
    rgb(29, 237, 10) 0%,
    rgb(255, 0, 183) 50.52%,
    rgb(0, 141, 143) 100%
  );
  width: 85%;
  margin: auto;
  margin-top: 40px;
  margin-bottom: 100px;
  padding-top: 4px;
  border-radius: 10px;
}

.title {
  background-color: rgb(1, 0, 0);
  border-radius: 10px 10px 8px 8px;
  padding: 23px 0px;
}
.title h1 {
  color: rgb(255, 255, 255);
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-size: 30px;
  font-weight: 300;
}

section {
  width: 70%;
  height: 150px;
  background-color: rgb(38, 20, 97);
  border-radius: 10px;
  margin: auto;
  margin-top: 30px;
  display: flex;
  align-items: center;
}

section .Image {
  margin-left: 30px;
}

section .Image img {
  width: 70px;
}

section .Infos {
  margin-left: 30px;
  font-family: "Poppins", sans-serif;
}
section .Infos h1 {
  font-size: 30px;
  color: aliceblue;
  font-weight: 500;
}
section .Infos h2 {
  font-size: 22px;
  color: aliceblue;
  font-weight: 300;
}

.footer {
  margin-top: 60px;
  width: 100%;
  height: 90px;
  display: flex;
  align-items: center;
  justify-content: center;

}

.footer p {
  color: aliceblue;
  font-family: "Poppins", sans-serif;
  font-size: 22px;
}

@media screen and (max-width: 480px) {
  .title h1 {
    font-size: 25px;
  }

  section .Image img {
    width: 60px;
  }

  section .Infos h1 {
    font-size: 25px;
    color: aliceblue;
    font-weight: 500;
  }
  section .Infos h2 {
    font-size: 16px;
    color: aliceblue;
    font-weight: 300;
  }
}

@media screen and (max-width: 376px) {
  .title h1 {
    font-size: 25px;
  }

  section {
    height: 100px;
  }

  section .Image img {
    width: 50px;
  }

  section .Infos h1 {
    font-size: 16px;
  }
  section .Infos h2 {
    font-size: 13px;
  }
}
